.positioning {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 20px;
  }
  .name {
    margin: 24px 0;

    font-size: 24px;
    font-weight: 700;
  }
  .form {
    display: flex;
    align-items: center;
    position: relative;
  }
  
  .input {
    background-color: #fff;
    height: 44px;
    padding: 10px 20px;
    border: none;
    border-radius: 40px;
    box-shadow: 3px 8px 14px 0px #88c6fd30;
  
    font-size: 16px;
    width: 280px;
    outline: none;
  }
  .icon {
    padding: 3px;
    position: absolute;
    right: 7px;
    top: 4px;
  }
  .icon::after {
    content: "";
    position: absolute;
    right: 8px;
    top: 10px;
    width: 17px;
    height: 17px;
    border: 1px solid #007bff;
    border-radius: 50%;
    transform: rotate(45deg);
  }
  
  .icon::before {
    content: "";
    position: absolute;
    right: 8px;
    top: 25px;
    width: 1px;
    height: 5px;
    border: 1px solid #007bff;
    transform: rotate(135deg);
  }

  .buttonClose{
    padding: 3px;
    position: absolute;
    right: 30px;
    top: 8px;
  }

  
  
 
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    .name {
      margin: 40px 0;
    }
    .positioning {
      margin-bottom: 43px;
    }
    .input {
      width: 608px;
    }
  }
  
 
  @media screen and (min-width: 1280px) {
    .positioning {
      margin-bottom: 43px;
    }
    .name {
      margin: 40px 0;
    }
    .input {
      width: 608px;
    }
  }
  

